import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { compose, withState } from 'recompose';
import withRequest from '../../utils/with-request';
import SpacesService from '../../services/spaces';
import SpacesCard from './SpacesCard';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import Loading from '../../components/Loading';
import styles from './styles';

const Spaces = memo(({
  classes, alert, setAlert, spacesData, spacesError, spacesLoading
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const spaces = _get(spacesData, 'spaces');
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} error={spacesError} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside className={classes.headerTitle}>
              <Typography variant="h4"><b>Reserva de espaos</b></Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <Grid container spacing={1}>
            {spacesLoading ? <Loading /> : spaces.map(space => <SpacesCard space={space} />)}
          </Grid>
        </div>
      </section>
    </div>
  );
});

Spaces.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  spacesLoading: PropTypes.bool,
  spacesError: PropTypes.object,
  spacesData: PropTypes.array
};

const withSpaces = withRequest(SpacesService.getSpaces, {
  name: 'spaces'
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withSpaces
)(Spaces);
