import Api from '../utils/api';

const Spaces = {
  getSpace(spaceId) {
    return Api.get(`/spaces/${spaceId}`);
  },
  getSpaces(buildingId) {
    // return Api.get(`/buildings/${buildingId}/spaces`);
    return new Promise((resolve) => {
      return resolve({ spaces: [
        { name: 'Auditrio A', photo_url: 'data:image/jpeg;base64,' },
        { name: 'Auditrio B', photo_url: 'data:image/jpeg;base64,' }
      ] });
    });
  }
};

export default Spaces;
